import AppBar from '../components/AppBar2'
import Drawer from '../components/Drawer2';
import * as React from 'react';
import Box from '@mui/material/Box';
import { styled, createTheme, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Toolbar from '@mui/material/Toolbar';
import Title from '../components/Title';
import Slider from '@mui/material/Slider';
import Typography from '@mui/material/Typography';
import axios from 'axios'
import zIndex from '@mui/material/styles/zIndex';
import Button from '@mui/material/Button';



//Das ist mein Base Template auf dem alle Pages außer eventuell der Landing page aufbauen werden


const Parameter = () => {

    const [open, setOpen] = React.useState(false);
    const [page, setPage] = React.useState("Parameter");
    const [value, setValue] = React.useState(0);
    const [params, setParams] = React.useState(null);
    
    const handleSliderChange = (e,index) => {
        setParams(params.map(param => {
            if (param.Indizes === index) {
              // Create a *new* object with changes
              return { ...param, Wartezeit: e.target.value.toString() };
            } else {
              // No changes
              return param;
            }
          }));
      };
    const handleClick = (event) => {
        console.log(params)
      };
    const defaultTheme = createTheme();
    
    const GetParams = async () => {
        try {
            const response = await axios.get('http://localhost:8000/getParamData', {})
            console.log("here")
            setParams(response.data)
            console.log(response.data)
            
        } catch (error) {
            console.log(error)
        }
    }
    
    const ParameterSchreiben = async () => {
        try {
            const response = await axios.put('http://localhost:8000/sendParamData', {params})
            console.log("schreibe Parameter in die Datenbank und von dort in die SPS")
            console.log(response.data)
            
            
        } catch (error) {
            console.log(error)
        }
    }
        
    
    
      React.useEffect(() => {
         GetParams()
         
      },[]) 



    return (
        
         <ThemeProvider theme={defaultTheme}>
            <Box sx={{ display: 'flex' }}>
                <CssBaseline />
                <AppBar open ={open} setOpen = {setOpen} page = {page}/>
                <Drawer open ={open} setOpen = {setOpen} page = {page}/>
                <Box
                component="main"
                sx={{
                    backgroundColor: (theme) =>
                    theme.palette.mode === 'light'
                        ? theme.palette.grey[100]
                        : theme.palette.grey[900],
                    flexGrow: 1,
                    height: '200vh',
                    overflow: 'auto',
                }}
                >
                    {/* Seitenspezifischer Content */}
                <Toolbar />
                <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
                    <Grid container spacing={3}>
                            {/* Chart */}
                            <Grid item xs={12} md={8} lg={12}>
                                <Paper
                                    
                                    sx={{
                                        width: '100%',
                                        p: 2,
                                        display: 'flex',
                                        flexDirection: 'column',
                                        height: 'auto',
                                        alignItems: 'center',
                                        textAlign: "center",
                                        verticalAlign: "middle",
                                        justifyContent: "center",
                                    }}
                                >
                                    <Title>Warte-Timer</Title>
                                    <Typography id="input-slider" gutterBottom>
                                        Hier kann für jeden Bahn die Wartezeit zwischen den Waferinspektionen eingestellt werden.
                                        Eine geringe Wartezeit führt zu einer erhöhten Taktfrequenz auf der augewählten Bahn.
                                    </Typography>
                                    {params?.map((param, _index) => (
                                    <Grid container spacing={2} alignItems="center"
                                    key={_index}
                                     sx ={{marginTop:'20px',
                                        width: '600px',
                                        }} >

                                        <Grid item sx={{ width:'200px' }}>
                                            <Box component="div" sx={{
                                                width:'50px', 
                                                display: 'inline',
                                                p: 1,
                                                m: 1,
                                                bgcolor: (theme) => (theme.palette.mode === 'dark' ? '#101010' : '#fff'),
                                                color: (theme) =>
                                                theme.palette.mode === 'dark' ? 'grey.300' : 'grey.800',
                                                border: '1px solid',
                                                borderColor: (theme) =>
                                                theme.palette.mode === 'dark' ? 'grey.800' : 'grey.300',
                                                borderRadius: 2,
                                                fontSize: '0.875rem',
                                                fontWeight: '700',}}>
                                                    {param?.Kameraname}
                                            </Box>
                                        </Grid>    
                                        <Grid item xs
                                            sx = {{
                                                width: '200px'
                 
                                            }}>
                                            <Slider
                                                value={typeof parseInt(params[_index].Wartezeit) === 'number' ? parseInt(params[_index].Wartezeit) : 0}
                                                onChange={(e) => handleSliderChange(e,param.Indizes)}
                                                aria-labelledby="input-slider"
                                                valueLabelDisplay="auto"
                                                step ={100}
                                                min ={0}
                                                max ={10000}
                                                color = "secondary"
                                
                                            />
                                        </Grid>
                                        <Grid item sx={{ width:'150px' }}>
                                        <Box component="div"
                                            onClick={handleClick} 
                                            sx={{
                                            width:'150px', 
                                            display: 'inline',
                                            p: 1,
                                            m: 1,
                                            bgcolor: (theme) => (theme.palette.mode === 'dark' ? '#101010' : '#fff'),
                                            color: (theme) =>
                                              theme.palette.mode === 'dark' ? 'grey.300' : 'grey.800',
                                            border: '1px solid',
                                            borderColor: (theme) =>
                                              theme.palette.mode === 'dark' ? 'grey.800' : 'grey.300',
                                            borderRadius: 2,
                                            fontSize: '0.875rem',
                                            fontWeight: '700',}}>
                                                {params[_index].Wartezeit} ms</Box>
                                        </Grid>
                                    </Grid>
                                    ))}
                                    <Button variant="contained" color="secondary" sx = {{ marginTop: '50px'}} onClick={ParameterSchreiben} >
                                         Werte in die SPS schreiben
                                         </Button>

                                
                            
                                </Paper>
                            </Grid>
                        </Grid>
                    </Container>


                </Box>
            </Box>
        </ThemeProvider>
    );
}
    
export default Parameter;